@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  overflow-style: overlay;
  background-color: #ffffff;
  font-family:'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
select:focus, textarea:focus, input:focus {
        font-size: 16px;
    }
}

.header__content {
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    margin: 0 auto;
    padding: 20px;
    color: rgb(255, 255, 255);
    font-size: 26px;
    font-weight: 500;
    align-items: center;
    justify-content: space-between;
    max-width: 1160px;
}
.header {
    background:url(/SoccerStat/static/media/header-bg.00cff39a.jpg);
    background-size: cover;
    background-position: 0 70%;

}
.header-title {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
}
.header-title:hover{
    text-decoration: underline;
}
.header-links {
    display: flex;
    justify-content: flex-end;
    
}
.header-link {
    margin-left: 20px;
    display: flex;
    text-decoration: none;
    color: white;
}
.header-link:hover {
    text-decoration: underline;
}
.header-logo {
    width: 50px;
    margin-right: 5px;
}
@media (max-width: 600px){
        .header__content {
           font-size: 13px; 
           padding: 20px 15px;
        }
        .header-logo {
            width: 20px;
        }
        .header-link {
            margin-left: 10px;
        }
}
@media (max-width: 320px){
        .header-title {
            align-items: flex-start;
        }
}
.app_content{
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 15px 0 15px;
}
a:hover {
  color: #e8f5e9;
}
.area-icon {
    height: 20px;
    width: 30px;
    padding-right: 10px;
}
.content {
    
    color: rgb(0, 0, 0);
    font-size: 16px;
    font-weight: bold;
    background-color: #e8f5e9;
    transition: 0.3s linear;
}
.content:nth-child(even) {
    background-color: #c8e6c9;
}
.content:hover{
    background-color: #a5d6a7;

}
.competition_area {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.content-td {
    padding: 20px 90px;
    
}
.content-td:first-child {
    border-radius: 6px 0 0 6px;
}
.content-td:last-child {
    border-radius: 0 6px 6px 0;
}

@media (max-width: 600px){
        .content {
           font-size: 10px; 
        }
        .area-icon {
            width: 15px;
            height: 10px;
        }
        .content-td {
             padding: 10px;
}
    }



    
.competitions-list {
    
    margin: 20px auto;
    width: 100%;
}
table{
    border-collapse: unset;
    border-spacing: 0 10px;
}
.search {
    box-sizing: border-box;
    width: 100%;
    margin-top: 20px;
    background-color: white;
    border-color: #4caf50;
    border-style: solid;
    border-width: 3px;
    border-radius: 6px;
    padding: 12px;
    font-size: 18px;
    outline: none;
}
@media (max-width: 600px){
    .search{
        padding: 6px;
        font-size: 12px;
    }
}
