.crest-icon {
    flex-grow: 0;
    flex-shrink: 0;
    height: 70px;
    width: 70px;
    margin-right: 20px;
}
.team-card {
    background-color: #e8f5e9;
    padding: 20px;
    border-radius: 6px;
    align-items: center;
    transition: 0.3s;
}
.team-content{
    display: flex;
}
.team-card:hover {
    background-color: #c8e6c9;
}
.team-link {
    text-decoration: none;
    color: black;
}
.team_name {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #1b5e20;
    text-decoration: none;
}
.team_item {
    margin-bottom: 3px;
}
.team_item span {
    font-weight: 500;
}
@media (max-width: 600px){
        .crest-icon {
            width: 45px;
            height: 45px;
            margin-right: 10px;
        }
        .team_name {
            font-size: 15px;
            margin-bottom: 5px;
        }
        .team_item {
            font-size: 12px;
        }
        .team-card {
            margin-bottom: 15px;
            
        }
}
    
.teams-list{
    margin: 20px auto;
    width: 100%;
    
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
}

@media (max-width: 600px) {
    .teams-list{
        display: block;
    }
}

    
.calendarItem-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-grow: 1;
}
.calendarItem {
    display: flex;
    flex-direction: column;
    padding: 15px;
    background-color: #e8f5e9;
    border-radius: 6px;
    
}
.calendarItem-name {
    color: #70757a;
    margin-bottom: 20px;
}
.calendarItem-score {
    min-width: 31px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-weight: 500;
}
.winner {
    font-weight: 700;
}
.arrowContainer {
    height: 20px;
    width: 20px;
}
.separatorTeam {
    width: 100%;
    height: 1px;
    background: radial-gradient(#70757a, #70757a00);
    margin: 7px auto
    ;
}
.team-container{
    display:flex;
    justify-content: space-between;
}
.calendarItem-info{
    flex-grow: 1;
    margin-right: 30px;
}
.calendarItem-data{
    text-align: center;
    padding: 0 20px;
    
}   
@media (max-width: 600px){
    .calendarItem-info{
        margin-right: 5px;
    }
    .calendarItem {
        padding: 10px;
    }
}

.calendarList {
    margin: 20px auto;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 5px;
}
.calendarList-title {
    margin: 20px 0 30px;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 26px;
}
.calendarList-error {
    /* width: 100%; */
    text-align: center;
    font-weight: bold;
    font-size: 26px;
    color: #1b5e20;
}
.calendarList-errorImg {
    display: block;
    height: 200px;
    margin: 20px auto;
}
.calendarList-errorBack {
    width: 250px;
    display: block;
    padding: 10px;
    margin: 10px auto;
    text-align: center;
    font-size: 20px;
    background-color: #e8f5e9;
    border-radius: 6px;
    color: #1b5e20;
    transition: 0.3s;
    text-decoration: none;
}
.calendarList-errorBack:hover {
   background-color: #c8e6c9;
   color: #1b5e20;
}
@media (max-width: 600px){
    .calendarList-error {
        font-size: 16px;
    }
    .calendarList-errorBack{
        font-size: 16px;
    }
    .calendarList-errorImg{
        width: 150px;
    }
    .calendarList-title {
       font-size: 16px; 
    }
}
.range-label {
    font-size: 16px;
    margin-bottom: 3px;
    font-weight: 500;
}
.filterMatches .ant-picker-active-bar{
    background: #1b5e20;
}
@media (max-width: 600px){
    .filterMatches {
        width: 100%;
    }
    .dropdownFilterMatches .ant-picker-panels{
        display: block;
        text-align: center;
    }
}



.mainPage-text span {
    font-weight: 700;
}
.mainPage-text{
    text-align: justify;
    font-size: 16px;
    width: 600px;
    margin: 0 auto;
}

.mainPage-links {
    display: flex;
    justify-content: center;
}
.mainPage-link{
    background-color: #e8f5e9;
    color: #1b5e20;
    font-size: 20px;
    padding: 10px 30px;
    margin: 20px;
    transition: 0.3s;
    font-weight: 500;
    border-radius: 6px;
}
.mainPage-link:hover {
   background-color: #c8e6c9;
   color: #1b5e20;
}
h1 {
    text-align: center;
    margin-top: 20px;
    font-size: 36px;
    font-weight: bold;
    color: #1b5e20;
}
.mainPage-content{
    display: flex;
}
.mainPage-cup{
    width: 600px;
    display: block;
    margin: 0 auto;
}
.mainPage-content p{
    margin-right: 20px;
}
@media (max-width: 600px){
    .mainPage-cup{
    width: 100%;
    }
    h1{
        font-size: 20px;
        margin-top: 10px;
    }
    .mainPage-link{
        display: block;
        font-size: 14px;
        /* padding: 5px 10px; */
        margin: 10px 0;
        text-align: center;
    }
    .mainPage-text{
    font-size: 10px;
    width: 100%;
    }
    .mainPage-links {
        display: block;
    }
}
